.multi-drop .selected-container .mag-container {
  margin-right: 22px;
  max-height: 32px;
  display: flex; }
  .multi-drop .selected-container .mag-container .mag-icon {
    align-items: center;
    filter: brightness(0) saturate(100%) invert(44%) sepia(11%) saturate(1407%) hue-rotate(140deg) brightness(89%) contrast(83%); }

.multi-drop .selected-container .selected-arrow {
  display: flex; }
  .multi-drop .selected-container .selected-arrow img {
    justify-content: center; }

.multi-drop .selected-container .selected-arrow:hover {
  filter: brightness(0) saturate(100%) invert(78%) sepia(45%) saturate(5238%) hue-rotate(121deg) brightness(96%) contrast(101%); }

.multi-drop .selected-container .selected-arrow:active {
  filter: brightness(0) saturate(100%) invert(44%) sepia(11%) saturate(1407%) hue-rotate(140deg) brightness(89%) contrast(83%); }

.multi-drop .selected-container .selected-items-container {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: center; }
  .multi-drop .selected-container .selected-items-container .selected-text-multi {
    height: 36px;
    border-radius: 200px;
    display: inline-block;
    width: auto;
    overflow: inherit;
    flex-grow: 1;
    padding-top: 5px; }
  .multi-drop .selected-container .selected-items-container .selected-item-container {
    height: 36px;
    border-radius: 200px;
    padding: 0 20px;
    margin: 7px;
    background-color: #C8E0E4;
    display: inline-block; }
    .multi-drop .selected-container .selected-items-container .selected-item-container .selected-item {
      display: flex;
      height: 100%; }
      .multi-drop .selected-container .selected-items-container .selected-item-container .selected-item p {
        text-align: center;
        align-items: center;
        justify-content: center;
        margin: auto;
        color: #354F53;
        font-weight: 600; }
      .multi-drop .selected-container .selected-items-container .selected-item-container .selected-item .selected-item-destroy {
        background: none;
        color: inherit;
        border: none;
        padding: 0;
        font: inherit;
        cursor: pointer;
        outline: inherit; }
        .multi-drop .selected-container .selected-items-container .selected-item-container .selected-item .selected-item-destroy img {
          margin-left: 20px;
          filter: brightness(0) saturate(100%) invert(44%) sepia(11%) saturate(1407%) hue-rotate(140deg) brightness(89%) contrast(83%); }
      .multi-drop .selected-container .selected-items-container .selected-item-container .selected-item .selected-item-destroy:hover {
        filter: brightness(0) saturate(100%) invert(78%) sepia(45%) saturate(5238%) hue-rotate(121deg) brightness(96%) contrast(101%); }
