/**
  * 
  * Fonts
  *
  */
$font-open-sans: 'Open Sans', sans-serif;
$font-after-night: 'After-Night', sans-serif;

$font-size-extra-big: 36px;
$font-size-big: 24px;
$font-size-regular: 18px;
$font-size-small: 16px;
$font-size-smallest: 14px;
$font-size-tiny: 12px;
$font-size-extra-tiny: 10px;

$font-weight-heavy: 800;
$font-weight-bold: bold;
$font-weight-semibold: 600;
$font-weight-normal: normal;
$font-weight-light: 200;

/**
  *
  * Colors
  *
  */
// Titles, menu, important
$color-title: #354F53;
$color-filter-title: brightness(0) saturate(100%) invert(27%) sepia(20%) saturate(630%) hue-rotate(140deg) brightness(95%) contrast(91%);

// Subheader, body
$color-body: #46737A;
$color-filter-body: brightness(0) saturate(100%) invert(44%) sepia(11%) saturate(1407%) hue-rotate(140deg) brightness(89%) contrast(83%);

// Captions, reminder, special note, Hover for white text
$color-caption: #99BBC1;
$color-filter-caption: brightness(0) saturate(100%) invert(83%) sepia(12%) saturate(494%) hue-rotate(141deg) brightness(85%) contrast(93%);

// Lines, outlines, boxes drawings
$color-line: #C8E0E4;
$color-filter-line: brightness(0) saturate(100%) invert(96%) sepia(6%) saturate(920%) hue-rotate(154deg) brightness(92%) contrast(94%);

// Alternative BG, Forms
$color-form: #F2FAF9;
$color-filter-form: brightness(0) saturate(100%) invert(95%) sepia(2%) saturate(1969%) hue-rotate(177deg) brightness(108%) contrast(96%);

// Hover, Highlight, Very important Interaction, Links
$color-hover: #00D7AF;
$color-filter-hover: brightness(0) saturate(100%) invert(78%) sepia(45%) saturate(5238%) hue-rotate(121deg) brightness(96%) contrast(101%);

// Alternative hover if colour already in green
$color-hover-alt: #36B29B;
$color-filter-hover-alt: brightness(0) saturate(100%) invert(64%) sepia(55%) saturate(492%) hue-rotate(119deg) brightness(84%) contrast(82%);

// Another green
$color-hover-alt2: #00edd8;
$color-filter-hover-alt2: brightness(0) saturate(100%) invert(68%) sepia(100%) saturate(1858%) hue-rotate(124deg) brightness(101%) contrast(106%);

$color-hover-alt3: #EAFFF5;
$color-filter-hover-alt3: brightness(0) saturate(100%) invert(90%) sepia(21%) saturate(193%) hue-rotate(85deg) brightness(105%) contrast(107%);

// Color error
$color-error: #ffa0b1;
$color-filter-error: brightness(0) saturate(100%) nvert(70%) sepia(4%) saturate(3156%) hue-rotate(301deg) brightness(98%) contrast(115%);

// Color error 2
$color-error-alt: #ff6779;
$color-filter-error: brightness(0) saturate(100%) invert(59%) sepia(11%) saturate(5978%) hue-rotate(313deg) brightness(100%) contrast(101%);

$color-error-alt-2: #d0021b;
$color-filter-error-alt-2: brightness(0) saturate(100%) invert(21%) sepia(71%) saturate(4228%) hue-rotate(342deg) brightness(76%) contrast(121%);

$color-error-alt-3: #FF6560;

$color-input-title-inactive: #B6BDCB;
$color-filter-input-title-inactive: brightness(0) saturate(100%) invert(85%) sepia(19%) saturate(155%) hue-rotate(182deg) brightness(86%) contrast(91%);

$color-input-cross: #919BAF;
$color-filter-input-cross: brightness(0) saturate(100%) invert(68%) sepia(3%) saturate(1666%) hue-rotate(182deg) brightness(91%) contrast(85%);

$color-light-green: #E2FFFA;
$color-light-grey: #AEDBDA;

$color-input-outline: #CADBEA;
$color-input-light-blue: #E0EAFE;
$color-input-text: #2C3652;

$color-loading: #433BC4 ;
$color-input-form-landing-desc: #6480EB;
$color-filter-input-form-landing-desc: brightness(0) saturate(100%) invert(52%) sepia(31%) saturate(5084%) hue-rotate(208deg) brightness(98%) contrast(88%);

$color-input-form-landing-desc2: #6E7F95;
$color-filter-input-form-landing-desc2: brightness(0) saturate(100%) invert(50%) sepia(28%) saturate(292%) hue-rotate(174deg) brightness(92%) contrast(94%);

$color-input-form-landing-desc3: #58C1A4;
$color-filter-input-form-landing-desc3: brightness(0) saturate(100%) invert(75%) sepia(9%) saturate(1955%) hue-rotate(113deg) brightness(88%) contrast(92%);

$color-dark-arrow: #DADADA;
$color-filter-dark-arrow: brightness(0) saturate(100%) invert(74%) sepia(83%) saturate(0%) hue-rotate(155deg) brightness(93%) contrast(116%);

$color-input-form-landing-desc-title: rgba(231, 235, 253, 0.397);
$color-input-form-landing-desc-title2: #F2FDFB;


$color-other-desc: #B6BDCB;
