/**
 * DropDown.css
 * CSS for custom form dropdown menu with input
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
 @import '../../../base';

.input-wrapper-main {
  width: 100%;
  margin: 10px 0;
  position: relative;

  @media screen and (max-width: 576px) {
    margin-bottom: 5px;
  }

  .input-wrapper-desc {
    font-size: $font-size-regular;
    font-weight: $font-weight-normal;
    color: $color-body;
    margin: 0;
    min-height: 27px;
    margin-left: 2px;

    @media screen and (max-width: 576px) {
        min-height: 0;
    }
  }

  .input-wrapper-error-text {
    margin-top: 12px;
    margin-left: 5px;
    color: $color-error-alt;
    font-size: $font-size-regular;
    font-weight: $font-weight-semibold;
  }

  .extra-info-container {
    p {
      font-size: $font-size-small;
      font-weight: $font-weight-light;
      color: $color-caption;
    }

    b {
      font-weight: $font-weight-semibold;
    }

    .green {
      color: $color-hover;
    }
  }

  .error {
    transition: all 0.3s ease;
    box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.15);
    background-image: linear-gradient($color-form, $color-form),
      linear-gradient(to bottom, $color-error, $color-error-alt) !important;
  }
}
