/**
 * DropDown.css
 * CSS for custom form dropdown menu with input
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
 @import '../../../base';

.dropdown {
  width: 100%;
  // margin: 10px 0;

  .dropdown-container {
    border-radius: $border-radius-main;
    background-image: linear-gradient($color-form, $color-form);
    background-origin: border-box;
    background-clip: content-box, border-box;
    border: solid 2px transparent;
    padding: 0;
    // margin: 10px 0;

    &:focus {
      outline: none;
    }
  }
  
  .dropdown-container-closed {
    transition: all 0.3s ease;
  }
  
  .dropdown-container:hover,
  .dropdown-container-opened {
    transition: all 0.3s ease;
    box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.15);
    background-image: linear-gradient($color-form, $color-form),
      linear-gradient(101deg, $color-hover, $color-body);
  }
  
  .error {
    transition: all 0.3s ease;
    box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.15);
    background-image: linear-gradient($color-form, $color-form),
      linear-gradient(to bottom, $color-error, $color-error-alt) !important;
  }
  
  .text-style {
    font-family: 'Open Sans', sans-serif;
    font-size: $font-size-regular;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: -0.3px;
    text-align: left;
    color: $color-title;
    cursor: default;
    white-space: nowrap;
  }
  
  .selected-container {
    width: 100%;
    height: 100%;
    display: flex;
    padding: 0 20px;

    .selected-text {
      overflow: hidden;
      width: 100%;
    }
    
    .selected-text input {
      width: 100%;
      border: none;
      background: transparent;
      color: $color-title;
      font-weight: $font-weight-semibold;
    }

    &:focus,
    .selected-text input:focus {
      outline: none;
    }
    
    .selected-text input::placeholder {
      color: $color-line;
    }
    
    .selected-arrow {
      padding-left: 20px;
      display: flex;
    }
    
    .selected-arrow-style {
      margin-top: 0px;
    }
    
    .selected-arrow .angle-up {
      transform: rotate(-180deg);
    }

    .selected-items-container {
      width: 100%;
      // margin-top: -10px;
      display: flex;
      flex-wrap: wrap;
      align-items: center;
    }
  }
  
  
  .list-container {
    width: 100%;
    text-align: center;
    margin-top: 2px;
    background: white;
    padding: 0;
    list-style-type: none;

    .list-item {
      display: flex;
    }
    
    .list-item:focus {
      outline: none;
    }
    
    .list-item:focus,
    .list-item:hover {
      background: $color-form;
    }
    
    .list-item p {
      display: flex;
      align-items: center;
      justify-content: left;
      padding-left: 20px;
      width: 100%;
      height: 100%;
    }
  }
  
  .list-closed {
    margin-top: 0;
    max-height: 0;
    transition: all 0.3s ease;
    border-radius: 8px;
  }
  
  .list-opened {
    transition: all 0.3s ease;
    border-radius: 8px;
  }
}
